import { useState } from "react";
import { useRouter } from "next/router";
import { v4 as uuidv4 } from "uuid";
import Image from "next/image";
import Hero from "../public/995.jpg";

export default function Home() {
  const [roomId, setRoomId] = useState("");
  const [username, setUsername] = useState("");
  const router = useRouter();

  const generateRoomId = () => {
    const newRoomId = uuidv4();
    setRoomId(newRoomId);

    if (
      navigator.clipboard &&
      typeof navigator.clipboard.writeText === "function"
    ) {
      navigator.clipboard
        .writeText(newRoomId)
        .then(() => alert("Room ID copied to clipboard!"))
        .catch(() => alert("Copy manually: " + newRoomId));
    } else {
      // Fallback for unsupported browsers
      const textArea = document.createElement("textarea");
      textArea.value = newRoomId;
      document.body.appendChild(textArea);
      textArea.select();
      try {
        document.execCommand("copy");
        alert("Room ID copied to clipboard!");
      } catch {
        alert("Copy manually: " + newRoomId);
      }
      document.body.removeChild(textArea);
    }
  };

  const joinRoom = () => {
    if (roomId && username) {
      router.push(`/editor/${roomId}?username=${username}`);
    } else {
      alert("Enter username and room ID");
    }
  };

  return (
    <div className="flex flex-col items-center w-full mx-auto p-0 m-0">
      <h1 className="text-2xl font-bold mb-6">Collaborative Code Editor</h1>

      {/* User input section */}
      <div className="flex flex-col sm:flex-row gap-4 w-full mb-6">
        <input
          className="p-2 border rounded flex-1"
          placeholder="Username"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
        <input
          className="p-2 border rounded flex-1"
          placeholder="Room ID"
          value={roomId}
          onChange={(e) => setRoomId(e.target.value)}
        />
      </div>

      {/* Buttons section */}
      <div className="flex gap-8 mb-8 w-full">
        <button
          className="bg-blue-500 text-white p-2 rounded flex-1"
          onClick={joinRoom}
        >
          Join Room
        </button>
        <button
          className="bg-green-500 text-white p-2 rounded flex-1"
          onClick={generateRoomId}
        >
          Create Room
        </button>
      </div>

      {/* Hero image section */}
      <div className="w-[35%] h-fit overflow-hidden rounded">
        <Image
          src={Hero}
          alt="Collaborative coding"
          width={800}
          height={500}
          className="object-cover"
        />
      </div>
    </div>
  );
}
